import TabPanel from './tab-panel.vue'
import { findIndex } from '../../common/helpers/util'
import { prefixStyle } from '../../common/helpers/dom'

const TRANSFORM = prefixStyle('transform')

const COMPONENT_NAME = 'cube-tab-panels'

export default {
  name: COMPONENT_NAME,
  props: {
    value: {
      type: [String, Number]
    },
    data: {
      type: Array,
      default () {
        return []
