const COMPONENT_NAME = 'cube-sticky-ele'

export default {
  name: COMPONENT_NAME,
  inject: ['sticky'],
  props: {
    eleKey: {
      type: [Number, String]
    }
  },
  mounted() {
    this.sticky.addEle(this)
  },
  methods: {
    refresh() {
      const el = this.$el
      if (el.firstElementChild) {
        el.style.height = ''
        el.style.height = `${el.offsetHeight}px`
      }
    }
  },
  beforeDestroy() {
    this.sticky.removeEle(this)
  }
}



