import CubeToolbarItem from './toolbar-item.vue'

const COMPONENT_NAME = 'cube-toolbar'
const EVENT_CLICK = 'click'
const EVENT_MORE_CLICK = 'more-click'

export default {
  name: COMPONENT_NAME,
  components: {
    CubeToolbarItem
  },
  props: {
    actions: {
      type: Array,
      default() {
        /* istanbul ignore next */
        return []
      }
    },
    moreActions: {
      type: Array
    }
  },
  data() {
    return {
      showMore: false
    }
  },
  computed: {
    basicActions() {
      const basicActions = this.actions.slice()
      this.moreActions && basicActions.push({
        icon: 'cubeic-more',
        $cubeMore: true
      })
      return basicActions
    }
  },
  methods: {
    itemClick(action) {
      if (action.$cubeMore) {
        this.showMore = !this.showMore
        this.$emit(EVENT_MORE_CLICK, this.showMore)
      } else {
        this.$emit(EVENT_CLICK, action)
      }
    }
  }
}



