const COMPONENT_NAME = 'cube-upload-btn'

export default {
  name: COMPONENT_NAME,
  props: {
    multiple: {
      type: Boolean,
      default: true
    },
    accept: {
      type: String,
      default: 'image/*'
    }
  },
  methods: {
    changeHandler(e) {
      const fileEle = e.currentTarget
      const files = fileEle.files
      if (files) {
        this.$parent.addFiles(files)
        fileEle.value = null
      }
    }
  }
}



