import CubeRadio from './radio.vue'
const COMPONENT_NAME = 'cube-radio-group'

const EVENT_INPUT = 'input'

export default {
  name: COMPONENT_NAME,
  props: {
    value: [String, Number],
    options: {
      type: Array,
      default() {
        return []
      }
    },
    position: {
      type: String,
      default: 'left'
    },
    horizontal: {
      type: Boolean,
      default: false
    },
    hollowStyle: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      radioValue: this.value
    }
  },
  watch: {
    value(newV) {
      this.radioValue = newV
    },
    radioValue(newV) {
      this.$emit(EVENT_INPUT, newV)
    }
  },
  computed: {
    _groupClass() {
      if (!this.horizontal) {
        return 'border-top-1px border-bottom-1px'
      }
    }
  },
  components: {
    CubeRadio
  }
}



