

import Vue from 'vue'
import App from './App'


import 'normalize.css'
import 'animate.css'
import '@/static/less/index.less'



import '@/components'
import 'amfe-flexible'


import {router} from '@/router'


import '@/extends'


import '@/service/mock'


import store from '@/logic'
import Cube from 'cube-ui'

import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'



Vue.use(AjaxPlugin)
Vue.use(ToastPlugin)
Vue.use(AlertPlugin)

Vue.use(Cube)
Vue.config.productionTip = false

const root = '/platform/api/'

Vue.mixin({
data() {
return {
api: require('./config/api.js')
}
},
methods: {
request(url, data = {}, method = "POST", header = "application/x-www-form-urlencoded") {
let vux = this;
let config = {
method: method,
url: root + url,
data,
params: data,
headers: {
'Content-Type': header,
'X-Nideshop-Token': store.getters.token
}
}
if (method == 'post') {
config.params = ''
} else {
config.data = ''
}
return new Promise(function (resolve, reject) {
vux.$http(config)
.then(res => {
if (res.status == 200) {

if (res.data.errno == 401) {

vux.$vux.toast.text('请登录后继续')







} else {
resolve(res.data);
}
} else {
reject(res.errMsg);
}
})
.catch(e => {
console.log(e)
})
})
}
}
})

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: {App},
template: '<App/>'
})



