import CubePopup from '../popup/popup.vue'
import visibilityMixin from '../../common/mixins/visibility'
import popupMixin from '../../common/mixins/popup'

const COMPONENT_NAME = 'cube-dialog'
const EVENT_CONFIRM = 'confirm'
const EVENT_CANCEL = 'cancel'
const EVENT_CLOSE = 'close'

const defHref = 'javascript:;'
const defConfirmBtn = {
  text: '确定',
  active: true,
  disabled: false,
  href: defHref
}
const defCancelBtn = {
  text: '取消',
  active: false,
  disabled: false,
  href: defHref
}
const parseBtn = (btn, defBtn) => {
  if (typeof btn === 'string') {
    btn = {
      text: btn
    }
  }
  return Object.assign({}, defBtn, btn)
}

export default {
  name: COMPONENT_NAME,
  mixins: [visibilityMixin, popupMixin],
  props: {
    type: {
      type: String,
      default: 'alert'
    },
    icon: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    showClose: {
      type: Boolean,
      default: false
    },
    confirmBtn: {
      type: [Object, String],
      default() {
        return {
          ...defConfirmBtn
        }
      }
    },
    cancelBtn: {
      type: [Object, String],
      default() {
        return {
          ...defCancelBtn
        }
      }
    }
  },
  data() {
    return {
      defHref
    }
  },
  computed: {
    _confirmBtn() {
      return parseBtn(this.confirmBtn, defConfirmBtn)
    },
    _cancelBtn() {
      return parseBtn(this.cancelBtn, defCancelBtn)
    },
    isConfirm() {
      return this.type === 'confirm'
    },
    containerClass() {
      return `cube-dialog-${this.type}`
    }
  },
  methods: {
    maskClick(e) {
      this.maskClosable && this.cancel(e)
    },
    confirm(e) {
      if (this._confirmBtn.disabled) {
        return
      }
      this.hide()
      this.$emit(EVENT_CONFIRM, e)
    },
    cancel(e) {
      if (this._cancelBtn.disabled) {
        return
      }
      this.hide()
      this.$emit(EVENT_CANCEL, e)
    },
    close(e) {
      this.hide()
      this.$emit(EVENT_CLOSE, e)
    }
  },
  components: {
    CubePopup
  }
}



