const COMPONENT_NAME = 'cube-switch'

const EVENT_INPUT = 'input'

export default {
  name: COMPONENT_NAME,
  props: {
    value: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      checkboxValue: this.value
    }
  },
  watch: {
    value (newVal) {
      this.checkboxValue = newVal
    },
    checkboxValue (newVal) {
      this.$emit(EVENT_INPUT, newVal)
    }
  }
}



