export default {
  name: 'm-button',
  props: {
    type: {
      type: String,
      default () {
        return 'default'
      }
    },
    color: {
      type: String,
      default () {
        return '#666'
      }
    },
    styles: {
      type: String
    }
  },
  computed: {
    calcStyles () {
      return `border-color: ${this.color};color:${this.color === '#666' ? '#666' : 'white'};background: ${this.color === '#666' ? null : this.color};${this.styles}`
    }
  }
}



