export default{
  name: 'm-number',
  props: ['value', 'params'],
  methods: {
    sub () {
      this.$emit('change', {type: 'sub', number: this.value, goods: this.params})
    },
    add () {
      this.$emit('change', {type: 'add', number: this.value, goods: this.params})
    }
  }
}



